@use '../../sass/shared/mixins';



.text {
  font-size: 18px;
  line-height: 1.4;
  margin-bottom: 32px;
  max-width: 280px;

  @include mixins.mq(tablet) {
    font-size: 34px;
    margin-bottom: 68px;
    max-width: 530px;
  }
}

.wrapper {
  @include mixins.mq(tablet-only) {
    height: 100vh;
    background-image: url('../../images/mainPage/tab/strawberry.png'),
      url('../../images/mainPage/tab/banana.png'), url('../../images/mainPage/tab/leaves.png'),
      url('../../images/mainPage/tab/stain.svg');

    background-size: 308px 303px, 491px 431px, 602px 531px, 521px 563px;
    background-position: right 24px bottom 168px, right 0 bottom 0,
      right 166px bottom -50px, right 0 bottom -4px;

    background-repeat: no-repeat;
  }

  @include mixins.mq(tablet) {
    padding-top: 100px;
  }

  @include mixins.mq(desktop) {
    height:calc(100vh - 255px);
    padding-top: 147px;
    background-image: url('../../images/mainPage/desk/leaves-desk.png'),
      url('../../images/mainPage/desk/banana.png'),
      url('../../images/mainPage/desk/strawberry.png'),
      url('../../images/mainPage/desk/stain.svg');

    background-size: 746px 820px, 715px 510px, 386px 379px, 715px 849px;
    background-position: right 195px top -100px, right -247px top -170px,
      right -8px top 400px, right -113px top 0px;
    background-repeat: no-repeat;
  }
}
